.App{
  background: radial-gradient(98.96% 98.96% at 50% 0%, #232A34 0%, #181E27 100%);
  padding: 1.25rem;
  border-radius: 0.625rem ;
}
.submitBtn{
  background: rgba(252, 118, 20, 1);
  transition: all 0.15s ease-in-out;
} 
.submitBtn:hover{
      background-color: white;
      color: orange;
}
.p-rating{
  color: orange;
}
@media (min-width: 500px){
    .App{
      width: 400px;
    }
}
